  .heading-stockist-table{
    width: 200px !important;
    height: 60px;
  }

  .stockist-table-container{
    overflow: auto !important;
    margin: 0px auto !important;
    min-height: 131px; /* Set a minimum height */
    max-height: 444px; /* Set a maximum height */
  }
  .tablet-stockist-table-head{
    height: 40px !important;
  }
  .tablet-stockist-table-head .MuiTableCell-root {
    height: 29px;
    padding: 2px 2px;
    background-color: transparent;
    font-weight: bold;
    color: #000;
  }
  
  /* For better alignment of table header content */
  .tablet-stockist-table-head .MuiTableCell-root {
    color: var(--Purple-600, #101011);
    text-align: center;
    
    /* Heading/X small */
    
    font-size: 13px !important;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  /* Quarter cell styling */
  .tablet-stockist-table-quarter-cell {
    width: 48px;
    height: 96px;
    text-align: center !important;
    color: #1d1e1e;
    border: 1px solid #e6e6e6;
    background-color: #f5f5f5;
    font-weight: bold !important;
    font-size: 12px !important;
    letter-spacing: 0.15px !important;
  }
  
  /* Base styles for all table cells */
  .tablet-stockist-table-cell {
    padding: 4px 8px;
    border: 1px solid #e6e6e6;
    color: #1d1e1e;
    text-align: center !important;
   
    font-size: 12px !important;
    font-style: normal;
    line-height: 14px !important;
    letter-spacing: 0.15px !important;
  }
  
  .tablet-stockist-table-cell span {
    
  font-size: 14px !important;
  font-style: normal !important;
  font-weight: 700 !important;
    text-align: center !important;
    font-size: 14px !important;
    line-height: 14px !important;
    letter-spacing: 0.15px !important;
  }
