.Stockist-second-container{
    width: 96.5%;
    height: 87px;
    margin: 0px auto;
    align-items: center;
    justify-content: center;
    justify-content: space-between;
    background-color: #f6f8f9;
    border: 1px solid #CACACA;
}
.Stockist-table-container{
    overflow: auto !important;
    margin: 0px auto !important;
    min-height: 131px; /* Set a minimum height */
    max-height: 444px; /* Set a maximum height */
    border: 1px solid #a9a6a6;
}
.Stockist-title{
    margin-top: 30px !important;
    color: #103460 !important;
    font-family: Roboto;
    font-size: 18px !important;
    font-style: normal !important;
    font-weight: 700 !important;
    line-height: 24px; /* 133.333% */
    letter-spacing: 0.15px;
}

.Stockist-download-button {
    width: 180px;
    height: 40px;
    font-size: 16px !important;
    padding: 15px 24px !important;
    margin: auto 0px !important;
    background-color: #3b459e !important;
    border-radius: 0px !important;
    text-transform: none !important;
  }
  
  .Stockist-download-button:hover {
    background-color: #2e3684 !important;
  }
  .stockist-fixed-table-container{
    width: 300px !important;
  }
  .stockist-table-head{
    height: 50px !important;
  }
  .stockist-table-head .MuiTableCell-root {
    height: 29px;
    padding: 8px 4px;
    border-bottom: 1px solid #ccc;
    background-color: #e9f4fa;
    color: #3b459e;
    font-weight: bold;
  }
  
  /* For better alignment of table header content */
  .stockist-table-head .MuiTableCell-root {
    color: var(--Purple-600, #3B459E);
    text-align: center;
    
    /* Heading/X small */
    
    font-size: 10px !important;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  /* Quarter cell styling */
  .stockist-table-quarter-cell {
    width: 48px;
    height: 96px;
    text-align: center !important;
    color: #1d1e1e;
    border: 1px solid #e6e6e6;
    background-color: #f5f5f5;
    font-weight: bold !important;
    font-size: 12px !important;
    letter-spacing: 0.15px !important;
  }
  
  /* Base styles for all table cells */
  .stockist-table-cell {
    padding: 4px 8px;
    border: 1px solid #e6e6e6;
    color: #1d1e1e;
    text-align: center !important;
   
    font-size: 12px !important;
    font-style: normal;
    line-height: 14px !important;
    letter-spacing: 0.15px !important;
  }
  
  .stockist-table-cell span {
    
  font-size: 14px !important;
  font-style: normal !important;
  font-weight: 700 !important;
    text-align: center !important;
    font-size: 14px !important;
    line-height: 14px !important;
    letter-spacing: 0.15px !important;
  }
 .scrollable-table-container{
width: 500px !important;
 }