/* BrandAnalysisStatus.module.css */

.container {
    width: 100%;
    padding: 16px;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  
  }
  
  .headerTitle {
    font-size: 16px !important;
    font-weight: 700 !important;
  }
  
.toggleButtonGroup{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}
  
  .mrStrength {
    font-size: 11px !important;
    padding: 4px 8px;
    color: #000; 
    border: 1px solid blue;
    
  }
  
  .mrStrengthStrong {
    font-weight: 700;
  }
  
  .cardsContainer {
    display: flex;
    gap: 16px;
  }
  
  .card {
    flex: 1;
    border-radius: 16px !important;
    overflow: hidden; /* Ensure rounded corners are respected */
  }
  
  .cardContentSales {
    padding: 16px 4px !important;
    background-color: #f2f6e4 !important;
    height: 209px !important;
    box-sizing: border-box;
  }
  
  .cardContentGrowth {
    padding: 16px 4px !important;
    background-color: #e5ebf6 !important;
    height: 209px !important;
    border-radius: 16px !important;
    box-sizing: border-box;
  }
  
  .cardTitle {
    font-size: 16px !important;
    font-weight: 700 !important;
    margin-bottom: 22px !important;
    color: #3b459e !important;
    text-align: center !important;
  }
  
  .table {
    width: 100%;
  }
  
  .tableHeadCell {
    font-size: 14px !important;
    padding: 4px 4px !important;
    border: none !important;
    font-weight: 700 !important;
    background-color: transparent !important;
  }
  
  .tableHeadCellEmpty {
    font-size: 14px !important;
    padding: 10px 4px !important;
    border: none !important;
    background-color: transparent !important;
  }
  
  .tableBodyCell {
    font-size: 12px !important;
    padding: 10px 4px !important;
    border: none !important;
    background-color: transparent !important;
  }
  
  .tableBodyCellBold {
    font-size: 12px !important;
    padding: 4px 4px !important;
    border: none !important;
    font-weight: 700 !important;
    background-color: transparent !important;
  }

  .radioGroup {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

/*  */


.radioButtonlabel{
  font-size: 12px !important;
}


.radioButton {
  padding: 10px; /* Further reduced padding around the radio button */
  width: 30px; /* Reduced width of the radio button */
  height: 20px; /* Reduced height of the radio button */
}


