
.view-mode-control {
    margin: 16px 0;
    display: flex;
    justify-content: center;
  }
  
  .table-container {
    width: 100%;
    max-height: 500px;
    margin: 0 auto;
    min-height: 100px;
    overflow-x: auto;
    overflow-y: auto;
    position: relative;
  }
  
  .table {
    /* min-width: 772px; */
    padding: 16px;
  }
  
  .header-row {
    background-color: #E9F4FA;
  }
  
  .sticky-header {
    position: sticky;
    top: 0;
    background-color: #E9F4FA;
    z-index: 3;
    width: 100px;
    min-width: 100px;
    max-width: 100px;
    font-size: 12px !important;
    white-space: normal;
    word-break: break-word;
    /* background-clip: padding-box; */
    color: #3B459E !important;
    /* border: 1px solid #E6E6E6; */
    text-align: center !important;
  }
  
  .zone-header {
    left: 0;
  }
  
  .region-header {
    left: 100px;
  }
  
  .hq-header {
    left: 100px;
  }
  
  .hq-header.with-region {
    left: 200px;
  }
  
  .label-column {
    position: sticky;
    left: 100px;
    top: 0;
    background-clip: padding-box;
    border: 1px solid #E6E6E6;
  }
  
  .label-column.one-visible {
    left: 200px;
  }
  .label-column.one-visible2 {
    left: 300px;
  }
  .label-column.both-visible {
    left: 300px;
  }
  .label-column.both-visible2 {
    left: 400px;
  }
  
  .month-header,.quater-header {
    position: sticky;
    top: 0;
    width: 100px;
    min-width: 100px;
    font-size: 12px !important;
    white-space: normal;
    word-break: break-word;
    /* border: 1px solid #E6E6E6; */
    background-color: #E9F4FA;
    z-index: 2;
    color: #3B459E !important;
  }
  
  .grand-total-header {
    position: sticky;
    top: 0;
    width: 100px;
    min-width: 100px;
    font-size: 12px !important;
    white-space: normal;
    word-break: break-word;
    /* border: 1px solid #E6E6E6; */
    background-color: #E9F4FA;
    z-index: 2;
    color: #3B459E !important;
  }
  
  .sticky-body {
    position: sticky;
    background-color: #F5F5F5;
    z-index: 1;
    width: 100px;
    min-width: 100px;
    max-width: 100px;
    font-size: 12px !important;
    white-space: normal;
    word-break: break-word;
    border: 1.5px solid #E6E6E6;
    background-clip: padding-box;
    
  }

  /* .sticky-body {
    position: sticky;
    background-color: #F5F5F5;
    z-index: 1;
    width: 100px;
    min-width: 100px;
    max-width: 100px;
    font-size: 10px !important;
    white-space: normal;
    word-break: break-word;
    box-shadow: 0 0 0 1.5px #E6E6E6; 
    background-clip: padding-box;
} */
.sticky-body {
  position: sticky;
  background-color: #F5F5F5;
  z-index: 1;
  width: 100px;
  min-width: 100px;
  max-width: 100px;
  font-size: 12px !important;
  white-space: normal;
  word-break: break-word;
  /* Remove the border */
  /* border: 1.5px solid #E6E6E6; */
  /* Use outline to add a line */
  outline: 1.5px solid #E6E6E6;
  outline-offset: -1.5px;
  background-clip: padding-box;
}
  
  .zone-cell {
    left: 0;
    
  }
  
  .region-cell {
    left: 100px;
    
  }
  
  .hq-cell {
    left: 100px;
  }
  
  .hq-cell.with-region {
    left: 200px;
  }
  
  .label-cell {
    position: sticky;
    background-color: #fff;
    border: 1px solid #E6E6E6;
    left: 100px;
    background-clip: padding-box;
    font-size: 14px !important;
   outline: none;
    /* font-style: normal !important;
    line-height: 14px !important; */

  }
  
  .label-cell.one-visible {
    left: 200px;
  }
  .label-cell.one-visible2 {
    left: 300px;
  }
  
  .label-cell.both-visible {
    left: 300px;
  }
  .label-cell.both-visible2 {
    left: 400px;
  }
  
  .month-cell {
    border: 1px solid #E6E6E6;
    width: 60px;
    /* min-width: 60px; */
    font-size: 14px !important;
    /* white-space: normal; */
    /* word-break: break-word; */
    white-space: nowrap;
    text-align: left !important;
   
  }
  
  .growth-cell.negative {
    color: red;
    font-weight: bold;
  }
  
  .growth-cell.positive {
    color: green;
    font-weight: bold;
  }
  
  .grand-total-cell {
    border: 1px solid #E6E6E6;
    /* width: 80px; */
    min-width: 127px;
    font-size: 14px !important;
    white-space: normal;
    word-break: break-word;
  }
  
  .cell-text {
    font-size: 12px !important;
    color: #3B459E !important;

  }

  .sticky-header.region-header , .sticky-header.zone-header {
    cursor: pointer;
  }
 
  .title-container {
    color: #3b459e;
    align-items: start;
  }
  
  .title {
    font-weight: 600 !important;
    font-size: 18px !important;
    letter-spacing: 0px !important;
    color: var(--Purple-600, #3B459E);
  
  /* Heading/X-large */
  
  font-style: normal !important;
  font-weight: 600 !important;
  }

  /* Download Button */
  .download-button {
    /* width: 180px; */
    height: 40px;
    font-size: 16px !important;
    padding: 15px 24px !important;
    margin: 0px !important;
    background-color: #3b459e !important;
    border-radius: 0px !important;
    text-transform: none !important;
    margin-top: 12px !important;
    
  }

.download-button:hover {
  background-color: #2e3684 !important;
}

.view-mode-label {
  font-weight: 400 !important;
  letter-spacing: 0px !important;
  color: var(--Text-Text-primary, #1D1E1E) !important;
text-align: center;

/* Body/X small */

font-size: 10px !important;
font-style: normal !important;
line-height: 12px !important; /* 120% */
}


.view-mode-control-radio .MuiSvgIcon-root {
  font-size: 12px !important;
}

.positive-text {
  color: green;
}

.negative-text {
  color: red;
}

.MuiBox-root.css-gmuwbf {
  justify-content: flex-start;
}

.month-cell.total-column {
  background-color: #F5F5F5 !important; /* Slightly darker background for content cells */
}

/* Style for Grand Total column in the table body (not header) */
.grand-total-cell {
  background-color: #F5F5F5 !important; /* Slightly darker background for content cells */
}

/* Style for Balance column in the table body (not header) */
.balance-cell {
  background-color: #F5F5F5 !important; /* Slightly darker background for content cells */
}
.total-cells{
  background-color: #F5F5F5 !important; 
}