.breakwise-container2 {
  margin: 0 auto;
  gap: 10px !important;
  padding: 10px;
  flex-direction: column;
  align-items: flex-start;
  width: 100% !important; /* Full width of the viewport */
  
}

.breakwise-container {
  width: 50%;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 12px;
  gap: 6px;
  box-shadow: none !important;
  display: flex;
  margin: 0;
  flex-direction: column;
}

.breakwise-stack {
  /* Add any necessary styles */
}

.breakwise-title-container {
  border-radius: 5px solid #797a7e !important;
}

.breakwise-title-text-2 {
  color: #103460 !important;
  font-size: 1.7vw !important; /* Responsive font size */
  font-style: normal;
  font-weight: 700 !important;
  line-height: 1.2em; /* Adjust line height */
  text-transform: capitalize !important;
}

.tablet-breakwise-button {
  width: 100%;
  max-width: 391px;
  height: 35px;
  text-align: left;
  color: rgba(0, 0, 0, 0.87) !important;
  font-size: 1.4vw; /* Responsive font size */
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.129px;
  justify-content: flex-start !important;
}

.tablet-breakwise-button-2 {
  color: rgba(0, 0, 0, 0.87) !important;
  text-align: center !important;
  font-size: 1.4vw !important; /* Responsive font size */
  font-style: normal;
  font-weight: 500 !important;
  line-height: normal;
  letter-spacing: 0.129px;
}

.tablet-breakwise-button:hover {
  color: grey;
  font-weight: 600 !important;
}
.tablet-breakwise-button-2:hover {
  color: grey;
  font-weight: 550 !important;
  
}
.breakwise-modal {
  /* Add any necessary styles */
}

.keep-button {
  color: rgba(0, 0, 0, 0.87) !important;
  font-size: 1.2vw; /* Responsive font size */
  width: 60px;
  border: 1px solid black;
}


.exclude-button {
  color: rgba(0, 0, 0, 0.87) !important;
  font-size: 1.2vw; /* Responsive font size */
  width: 60px;
  border: 1px solid black;
}
.tablet-info-box {
  font-size: 1.2vw !important; /* Responsive font size */
  height: auto;
  padding: 10px;
  width: 100%;
}
.breakwise-modal-content {
  width: 100%;
  border: 2px solid #c0c0c0;
  background: #e8e8e8;
}

.tablet-name {
  color: #000;
  padding: 4px;
}

.custom-tooltip {
  background-color: #f5f5f5;
  color: rgba(0, 0, 0, .87);
  font-size: 1.2vw !important; /* Responsive font size */
  border: 0px solid #dadde9;
  width: 100%;
  max-width: 360px !important;
  box-shadow: none !important;
}
.bold-text {
  font-weight: bold;
  color: #000 !important;

}



