.breakwise-filter-container {
    width: 100%;
    min-height: 100px;
    height: auto;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  justify-content: space-between;
  background-color: #f6f8f9;
}

.breakwise-filter-icon {
    margin-top: 10px;
}

@media (max-width: 1024px) {
    .breakwise-filter-container {
        max-width: 100%;
    }
}

@media (max-width: 768px) {
    .breakwise-filter-container {
        flex-direction: column;
        align-items: center;
    }
}

@media (max-width: 480px) {
    .breakwise-filter-container {
        padding: 10px;
    }
}
