.breakwise-second-container {
  width: 96vw; /* Full width of the viewport */
  height: auto; /* Adjust height based on content */
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  background-color: #f6f8f9;
  border: 1px solid #cacaca;
  margin-left: 19px;
}

.breakwise-table-container {
  width: 96vw !important; /* Full width of the viewport */
  min-height: 131px;
  max-height: 644px;
  border-radius: 2px;
  border: 1px solid rgba(24, 29, 31, 0.15);
  background: #fff;
  overflow-y: auto !important;
  align-items: flex-start;
  justify-content: center;
  display: block;
}

.breakwise-title {
  margin-top: 30px !important;
  color: #103460 !important;
  font-family: Roboto;
  font-size: 1.5vw !important; /* Responsive font size */
  font-style: normal !important;
  font-weight: 700 !important;
  line-height: 1.2em; /* Adjust line height */
  letter-spacing: 0.15px;
}

.breakwise-download-button {
  width: 100%;
  max-width: 180px;
  height: 40px;
  font-size: 1.2vw !important; /* Responsive font size */
  padding: 15px 24px !important;
  margin: auto 0px !important;
  background-color: #3b459e !important;
  border-radius: 0px !important;
  text-transform: none !important;
  margin-right: 20px !important;
  color: #fff !important;
}

.breakwise-download-button:hover {
  background-color: #38418e !important;
}

.even {
  background-color: #ffffff !important;
}

.odd {
  background-color: #e6e6e6 !important;
}

.breakage-custom-font {
  color: #000 !important;
  font-size: 1.2vw !important; /* Responsive font size */
  font-style: bold;
  font-weight: 550 !important;
  line-height: 1.2em; /* Adjust line height */
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  border-left: 1px solid #e6e6e6;
  height: 28px;
  white-space: nowrap;
  overflow: hidden;
}

.breakwise-scrollable-table-container {
  width: 100%;
  max-width: 473px !important;
  overflow-y: hidden;
}

.fixed-table-container {
  width: 100vw !important; /* Full width of the viewport */
  min-height: 131px; /* Set a minimum height */
  max-height: 444px; /* Set a maximum height */
  overflow: auto !important;
  margin: 0px auto !important;
}

.fixed-table-head .MuiTableCell-root {
  height: 19px;
  padding: 8px 4px;
  border-bottom: 1px solid #ccc;
  background-color: #e9f4fa;
  color: #3b459e;
  font-weight: bold;
}

.fixed-table-head .MuiTableCell-root {
  color: var(--Purple-600, #3b459e);
  text-align: center;
  font-size: 1.2vw !important; /* Responsive font size */
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

@media (max-width: 1024px) {
  .breakwise-title {
    font-size: 2vw !important;
  }

  .breakwise-download-button, .breakage-custom-font, .fixed-table-head .MuiTableCell-root {
    font-size: 2vw !important;
  }
}

@media (max-width: 768px) {
  .breakwise-title {
    font-size: 3vw !important;
  }

  .breakwise-download-button, .breakage-custom-font, .fixed-table-head .MuiTableCell-root {
    font-size: 3vw !important;
  }
}

@media (max-width: 480px) {
  .breakwise-title {
    font-size: 4vw !important;
  }

  .breakwise-download-button, .breakage-custom-font, .fixed-table-head .MuiTableCell-root {
    font-size: 4vw !important;
  }
}

.breakwise-table-container::-webkit-scrollbar {
  width: 8px;
}